.tags-input-container {
    padding: .5em;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    align-items: right;
    flex-wrap: wrap;
    gap: .5em;
    max-width: 50%;
    min-width: 50%;
}

.tag-item {
    background-color: rgb(82, 82, 82);
    display: inline-block;
    padding: .1em .2em;
    margin-right: 1px;
    border-radius: 5px;
    flex-wrap: wrap;
    margin-bottom: .1em;
}

.tag-item .close {
    height: 20px;
    width: 20px;
    background-color: rgb(48, 48, 48);
    color: #fcfcfc;
    border-radius: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-left: .5em;
    font-size: 18px;
    cursor: pointer;
}

.tags-input {
    flex: 1;
    width: 100%;
    min-width: 100%;
}